.rootContainer{
  max-width: 960px;
  margin: auto;
}



.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.appContent{
  display: flex;
  flex-direction: column;
align-items: center;
}  

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
    position: fixed;
    z-index: 105;
    top: 0;
    right: 0;
  }
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.row {
  display: flex;
}

  
.ticket {
  position: fixed;
  z-index: 105;
  left: 0;
  bottom: 0;
}

.description {
  color: black;
  font-size: small;
  font-style: italic;
 }